import { useEffect, useState } from 'react';
import Form from 'react-bootstrap/Form';
import axios from 'axios';
import LoadingScreen from '../components/loadingScreen';
import Header from '../components/header';
import { Box, Button, Text, Input, Divider } from '@chakra-ui/react';
import { Card, CardGroup, Container, Modal, Row } from 'react-bootstrap';

export default function ResetPassword() {
    const [template, setTemplate] = useState({
        img: '',
        name: '',
        template: '',
        templateFileName: ''
    });
    const [method, setMethod] = useState('');
    const [temps, setTemps] = useState([]);
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [vshow, setVshow] = useState(false);
    const handleVclose = () => setVshow(false);
    const handleVshow = () => setVshow(true);
    const [templateId, setTemplateId] = useState();
    const [testLoading, setTestLoading] = useState(false);

    useEffect(async () => {
        await axios({
            method: 'GET',
            url: `/api/v1/user/admin`
        })
            .then((res) => {
                if (res.data.status === 'Success') return '';
            })
            .catch(() => {
                return window.location.assign('/');
            });

        await axios({
            method: 'GET',
            url: '/api/v1/resume/resumes'
        })
            .then((res) => {
                if (res.data.status === 'Success') {
                    setTemps(res.data.docs);
                }
            })
            .catch((err) => alert(err.response.data.message));
    }, []);

    const uploadFiles = async (id = '') => {
        const data = new FormData();
        await Promise.all(
            ['img', 'name', 'template', 'templateFileName'].map((el) => {
                if (method === 'post' || (method === 'patch' && !!template[el]))
                    data.append(el, template[el]);
            })
        );

        const finalData =
            method === 'post'
                ? {
                      method: 'POST',
                      url: `/api/v1/resume/upload-template`,
                      data
                  }
                : method === 'patch'
                ? {
                      method: 'PATCH',
                      url: `/api/v1/resume/manage-template/${template.id}`,
                      data
                  }
                : {
                      method: 'DELETE',
                      url: `/api/v1/resume/manage-template/${id}`
                  };

        await axios(finalData)
            .then((res) => {
                if (res.data.status === 'Success') {
                    alert('Your request process completed!');
                    return window.location.reload();
                }
            })
            .catch((err) => {
                alert(err.response.data.message);
            });
    };

    const downloadPDF = async (url, name) => {
        try {
            const response = await axios({
                url,
                method: 'GET',
                responseType: 'blob' // Important
            });

            const blob = new Blob([response.data], { type: 'application/pdf' });
            const link = document.createElement('a');
            link.href = window.URL.createObjectURL(blob);
            link.download = name; // Name the file as you wish
            link.click();
            setTestLoading(false);
        } catch (error) {
            setTestLoading(false);
            alert('Failed to download the PDF.');
        }
    };

    const testTemplate = async () => {
        setTestLoading(true);
        await axios({
            method: 'GET',
            url: `/api/v1/resume/test-template/${templateId}`
        })
            .then((res) => {
                if (res.data.status === 'Success') {
                    return downloadPDF(res.data.docs.url, res.data.docs.name);
                }
            })
            .catch((err) => {
                setTestLoading(false);
                alert(err.response.data.message);
            });
    };

    const manageTempateStatus = async (status) => {
        await axios({
            method: 'PATCH',
            url: `/api/v1/resume/template/${status}/${templateId}`
        })
            .then((res) => {
                if (res.data.status === 'Success') {
                    return alert('You process successfully Complated!!!');
                }
            })
            .catch((err) => {
                alert(err.response.data.message);
            });
    };
    return (
        <div style={{ width: '100%' }}>
            <LoadingScreen />
            <Header />

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Manage Templates</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Box display="flex" justifyContent={'center'} w={'100%'}>
                        <Form className="w-100">
                            <Form.Group
                                className="mb-3"
                                controlId="exampleForm.ControlTextarea1"
                            >
                                <Text fontWeight={'bold'}>Template Name</Text>
                                <Input
                                    required
                                    type="text"
                                    aria-describedby="passwordHelpBlock"
                                    value={template.name}
                                    onChange={(e) => {
                                        setTemplate({
                                            ...template,
                                            name: e.target.value
                                        });
                                    }}
                                />
                            </Form.Group>
                            <Form.Group
                                className="mb-3"
                                controlId="exampleForm.ControlTextarea1"
                            >
                                <Text fontWeight={'bold'}>Template Image</Text>
                                <Input
                                    required
                                    type="file"
                                    accept="image/*"
                                    aria-describedby="passwordHelpBlock"
                                    onChange={(e) => {
                                        setTemplate({
                                            ...template,
                                            img: e.target.files[0]
                                        });
                                    }}
                                />
                            </Form.Group>
                            <Form.Group
                                className="mb-3"
                                controlId="exampleForm.ControlTextarea1"
                            >
                                <Text fontWeight={'bold'}>
                                    Template FileName
                                </Text>
                                <Input
                                    required
                                    type="text"
                                    id="inputPassword5"
                                    aria-describedby="passwordHelpBlock"
                                    value={template.templateFileName}
                                    onChange={(e) => {
                                        setTemplate({
                                            ...template,
                                            templateFileName: e.target.value
                                        });
                                    }}
                                />
                            </Form.Group>
                            <Form.Group
                                className="mb-3"
                                controlId="exampleForm.ControlTextarea1"
                            >
                                <Text fontWeight={'bold'}>Template</Text>
                                <Input
                                    required
                                    type="file"
                                    aria-describedby="passwordHelpBlock"
                                    onChange={(e) => {
                                        setTemplate({
                                            ...template,
                                            template: e.target.files[0]
                                        });
                                    }}
                                />
                            </Form.Group>
                            <Button
                                colorScheme="blue"
                                w={'100%'}
                                onClick={uploadFiles}
                            >
                                Submit
                            </Button>
                        </Form>
                    </Box>
                </Modal.Body>
            </Modal>
            <Modal centered={true} show={vshow} onHide={handleVclose}>
                <Modal.Header closeButton>
                    <Modal.Title>Resume</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Box display={'flex'} gap={2}>
                        <Button
                            w={'100%'}
                            colorScheme="blue"
                            size={'sm'}
                            onClick={testTemplate}
                            isLoading={testLoading}
                        >
                            Test
                        </Button>
                        <Button
                            w={'100%'}
                            colorScheme="green"
                            size={'sm'}
                            onClick={() => manageTempateStatus('accept')}
                        >
                            Accept
                        </Button>
                        <Button
                            w={'100%'}
                            colorScheme="red"
                            size={'sm'}
                            onClick={() => manageTempateStatus('reject')}
                        >
                            Reject
                        </Button>
                    </Box>
                </Modal.Body>
                <Modal.Footer></Modal.Footer>
            </Modal>
            <Container>
                <Box
                    marginTop={10}
                    display={'flex'}
                    justifyContent={'space-between'}
                >
                    <Text fontSize={'20px'}>Templates</Text>
                    <Button
                        colorScheme="blue"
                        onClick={() => {
                            setMethod('post');
                            setTemplate({
                                img: '',
                                name: '',
                                template: '',
                                templateFileName: ''
                            });
                            handleShow();
                        }}
                    >
                        New Template
                    </Button>
                </Box>
                <Divider size={'lg'}></Divider>
                <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4 g-4 py-5">
                    {temps.map((el) => (
                        <div class="col d-flex align-items-start">
                            <Card>
                                <Card.Img variant="top" src={el.img} />
                                <Card.Body>
                                    <Card.Title>{el.name}</Card.Title>
                                    {/* <Card.Text>
                                        Some quick example text to build on the
                                        card title and make up the bulk of the
                                        card's content.
                                    </Card.Text> */}
                                    <Box display={'flex'} gap={2}>
                                        <Button
                                            w={'100%'}
                                            colorScheme="blue"
                                            onClick={() => {
                                                setTemplateId(el.temuId);
                                                handleVshow();
                                            }}
                                        >
                                            Verify
                                        </Button>
                                        <Button
                                            w={'100%'}
                                            colorScheme="green"
                                            onClick={() => {
                                                setMethod('patch');
                                                setTemplate({
                                                    img: '',
                                                    name: el.name,
                                                    template: '',
                                                    templateFileName:
                                                        el.templateFileName,
                                                    id: el.temuId
                                                });
                                                handleShow();
                                            }}
                                        >
                                            Edit
                                        </Button>
                                        <Button
                                            w={'100%'}
                                            colorScheme="red"
                                            onClick={() => {
                                                setMethod('delete');
                                                uploadFiles(el.temuId);
                                            }}
                                        >
                                            Delete
                                        </Button>
                                    </Box>
                                </Card.Body>
                            </Card>
                        </div>
                    ))}
                </div>
            </Container>
        </div>
    );
}
