import { useEffect, useState } from 'react';
import Form from 'react-bootstrap/Form';
import axios from 'axios';
import LoadingScreen from '../components/loadingScreen';
import Header from '../components/header';
import { Box, Button, Text, Input, Divider } from '@chakra-ui/react';
import { Card, CardGroup, Container, Modal, Row } from 'react-bootstrap';

export default function MyResumes() {
    const [loading, setLoading] = useState('');
    const [temps, setTemps] = useState([]);

    useEffect(async () => {
        await axios({
            method: 'GET',
            url: '/api/v1/user/my-resumes'
        })
            .then((res) => {
                if (res.data.status === 'Success') {
                    setTemps(res.data.docs);
                }
            })
            .catch((err) => window.location.assign('/'));
    }, []);

    const downloadPDF = async (url, name) => {
        try {
            const response = await axios({
                url,
                method: 'GET',
                responseType: 'blob' // Important
            });

            const blob = new Blob([response.data], { type: 'application/pdf' });
            const link = document.createElement('a');
            link.href = window.URL.createObjectURL(blob);
            link.download = name; // Name the file as you wish
            link.click();
        } catch (error) {
            alert('Failed to download the PDF.');
        }
        setLoading(false);
    };

    return (
        <div style={{ width: '100%' }}>
            <LoadingScreen />
            <Header />

            <Container>
                <Box
                    marginTop={10}
                    display={'flex'}
                    justifyContent={'space-between'}
                >
                    <Text fontSize={'20px'}>Templates</Text>
                    <Button
                        colorScheme="blue"
                        onClick={() => {
                            window.location.assign('/build');
                        }}
                    >
                        New Resume
                    </Button>
                </Box>
                <Divider size={'lg'}></Divider>
                <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4 g-4 py-5">
                    {temps.map((el, i) => (
                        <div class="col d-flex align-items-start">
                            <Card>
                                <Card.Img
                                    variant="top"
                                    src={el.resume?.length && el.resume[0]?.img}
                                />
                                <Card.Body>
                                    <Card.Title>{el.resumeName}</Card.Title>
                                    {/* <Card.Text>
                                        Some quick example text to build on the
                                        card title and make up the bulk of the
                                        card's content.
                                    </Card.Text> */}
                                    <Box display={'flex'} gap={2}>
                                        <Button
                                            w={'100%'}
                                            colorScheme="blue"
                                            onClick={async () => {
                                                setLoading(i);
                                                downloadPDF(
                                                    el.resumeUrl,
                                                    el.resumeName
                                                );
                                            }}
                                            isLoading={
                                                i === loading ? true : false
                                            }
                                        >
                                            Save
                                        </Button>
                                        <Button
                                            w={'100%'}
                                            colorScheme="green"
                                            onClick={() =>
                                                window.location.assign(
                                                    `/build?template=${el.rmuId}`
                                                )
                                            }
                                        >
                                            Edit
                                        </Button>
                                        <Button
                                            w={'100%'}
                                            colorScheme="red"
                                            onClick={async () => {
                                                await axios({
                                                    method: 'DELETE',
                                                    url: `/api/v1/user/resume/${el.rmuId}`
                                                })
                                                    .then((res) => {
                                                        if (
                                                            res.data.status ===
                                                            'Success'
                                                        ) {
                                                            window.location.reload();
                                                        }
                                                    })
                                                    .catch((err) =>
                                                        alert(
                                                            err.response.data
                                                                .message
                                                        )
                                                    );
                                            }}
                                        >
                                            Delete
                                        </Button>
                                    </Box>
                                </Card.Body>
                            </Card>
                        </div>
                    ))}
                </div>
            </Container>
        </div>
    );
}
