import { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';
import Dropdown from 'react-bootstrap/Dropdown';
import axios from 'axios';
import { baseURL } from '../util/baseURL';
import { Container } from 'react-bootstrap';
import imgLogo from '../logo.png';
import { Image } from '@chakra-ui/react';

export default function Header() {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [loginData, setLoginData] = useState({ email: '', password: '' });
    const [signupData, setSignupData] = useState({
        name: '',
        email: '',
        password: ''
    });
    const [forgetData, setForgetData] = useState({ email: '' });
    const [activeForm, setActiveForm] = useState();
    const [isUserLoggedIn, setUserLoginIn] = useState(false);

    useEffect(() => {
        let storedUser =
            localStorage.getItem('user') !== 'undefined'
                ? localStorage.getItem('user')
                : '{}';

        storedUser = JSON.parse(storedUser);

        if (storedUser?.email) setUserLoginIn(true);
    }, []);

    const makeLogin = async () => {
        const { url, data } =
            activeForm === 'login'
                ? { data: loginData, url: `${baseURL}/user/login` }
                : activeForm === 'signup'
                ? { data: signupData, url: `${baseURL}/user/signup` }
                : { data: forgetData, url: `${baseURL}/user/forgot-password` };
        await axios({
            method: 'POST',
            url: url,
            data
        })
            .then((res) => {
                if (activeForm !== 'forget') {
                    localStorage.setItem('user', JSON.stringify(res.data.data));
                    return window.location.reload();
                } else alert('Please check you email for verification link');
            })
            .catch((err) => alert(err.response.data.message));
    };
    const logout = async () => {
        await axios({
            method: 'GET',
            url: '/api/v1/user/logout'
        })
            .then((res) => {
                if (res.data.status === 'Success') {
                    localStorage.setItem('user', '{}');
                    window.location.reload();
                }
            })
            .catch((err) => alert(err.response.data.message));
    };
    return (
        <div>
            {!isUserLoggedIn ? (
                <Modal show={show} onHide={handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Enter your details</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        {activeForm === 'login' ? (
                            <Form>
                                <Form.Group
                                    className="mb-3"
                                    controlId="exampleForm.ControlInput1"
                                >
                                    <Form.Label>Email address</Form.Label>
                                    <Form.Control
                                        type="email"
                                        placeholder="name@example.com"
                                        autoFocus
                                        onChange={(e) =>
                                            setLoginData({
                                                ...loginData,
                                                email: e.target.value
                                            })
                                        }
                                    />
                                </Form.Group>
                                <Form.Group
                                    className="mb-3"
                                    controlId="exampleForm.ControlTextarea1"
                                >
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control
                                        type="password"
                                        id="inputPassword5"
                                        aria-describedby="passwordHelpBlock"
                                        onChange={(e) =>
                                            setLoginData({
                                                ...loginData,
                                                password: e.target.value
                                            })
                                        }
                                    />
                                    <Form.Text id="passwordHelpBlock" muted>
                                        Your password must be 8-20 characters
                                        long, contain letters and numbers, and
                                        must not contain spaces, special
                                        characters, or emoji.
                                    </Form.Text>
                                </Form.Group>
                                <Container>
                                    <p
                                        onClick={() => setActiveForm('forget')}
                                        style={{
                                            textAlign: 'right',
                                            cursor: 'pointer',
                                            color: 'blue'
                                        }}
                                    >
                                        <u>Forget Password?</u>
                                    </p>
                                </Container>
                            </Form>
                        ) : activeForm === 'signup' ? (
                            <Form>
                                <Form.Group
                                    className="mb-3"
                                    controlId="exampleForm.ControlInput1"
                                >
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder="John"
                                        autoFocus
                                        onChange={(e) =>
                                            setSignupData({
                                                ...signupData,
                                                name: e.target.value
                                            })
                                        }
                                    />
                                </Form.Group>
                                <Form.Group
                                    className="mb-3"
                                    controlId="exampleForm.ControlInput1"
                                >
                                    <Form.Label>Email address</Form.Label>
                                    <Form.Control
                                        type="email"
                                        placeholder="name@example.com"
                                        autoFocus
                                        onChange={(e) =>
                                            setSignupData({
                                                ...signupData,
                                                email: e.target.value
                                            })
                                        }
                                    />
                                </Form.Group>
                                <Form.Group
                                    className="mb-3"
                                    controlId="exampleForm.ControlTextarea1"
                                >
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control
                                        type="password"
                                        id="inputPassword5"
                                        aria-describedby="passwordHelpBlock"
                                        onChange={(e) =>
                                            setSignupData({
                                                ...signupData,
                                                password: e.target.value
                                            })
                                        }
                                    />
                                    <Form.Text id="passwordHelpBlock" muted>
                                        Your password must be 8-20 characters
                                        long, contain letters and numbers, and
                                        must not contain spaces, special
                                        characters, or emoji.
                                    </Form.Text>
                                </Form.Group>
                            </Form>
                        ) : (
                            <Form>
                                <Form.Group
                                    className="mb-3"
                                    controlId="exampleForm.ControlInput1"
                                >
                                    <Form.Label>Email address</Form.Label>
                                    <Form.Control
                                        type="email"
                                        placeholder="name@example.com"
                                        autoFocus
                                        onChange={(e) =>
                                            setForgetData({
                                                email: e.target.value
                                            })
                                        }
                                    />
                                </Form.Group>
                            </Form>
                        )}
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={makeLogin}>
                            {activeForm === 'login'
                                ? 'Login'
                                : activeForm === 'signup'
                                ? 'Signup'
                                : 'Submit'}
                        </Button>
                    </Modal.Footer>
                </Modal>
            ) : (
                ''
            )}

            <header class="p-3 text-bg-light">
                <div class="container">
                    <div class="d-flex flex-wrap align-items-center justify-content-center justify-content-lg-start">
                        <a
                            href="/"
                            class="d-flex align-items-center mb-2 mb-lg-0 text-dark text-decoration-none brand-name"
                        >
                            <Image
                                src={imgLogo}
                                h="46px"
                                alt="deepblogs logo"
                            />
                            <p
                                style={{
                                    paddingLeft: '10px',
                                    fontFamily: 'monospace',
                                    fontSize: '21px',
                                    paddingTop: '10px'
                                }}
                            >
                                DB-Resume
                            </p>
                        </a>

                        <ul class="nav col-12 col-lg-auto me-lg-auto mb-2 justify-content-center mb-md-0">
                            {/* <li>
                                <a
                                    href="#"
                                    class="nav-link px-2 text-secondary"
                                >
                                    Home
                                </a>
                            </li>
                            <li>
                                <a href="#" class="nav-link px-2 text-dark">
                                    Features
                                </a>
                            </li>
                            <li>
                                <a href="#" class="nav-link px-2 text-dark">
                                    Pricing
                                </a>
                            </li>
                            <li>
                                <a href="#" class="nav-link px-2 text-dark">
                                    FAQs
                                </a>
                            </li>
                            <li>
                                <a href="#" class="nav-link px-2 text-dark">
                                    About
                                </a>
                            </li> */}
                        </ul>

                        {!isUserLoggedIn ? (
                            <div class="text-end">
                                <button
                                    onClick={() => {
                                        setActiveForm('login');
                                        handleShow();
                                    }}
                                    type="button"
                                    class="btn btn-outline-primary me-2"
                                >
                                    Login
                                </button>
                                <button
                                    onClick={() => {
                                        setActiveForm('signup');
                                        handleShow();
                                    }}
                                    type="button"
                                    class="btn btn-primary"
                                >
                                    Sign up
                                </button>
                            </div>
                        ) : (
                            <div class="flex-shrink-0 dropdown">
                                <Dropdown>
                                    <Dropdown.Toggle
                                        variant="danger"
                                        id="dropdown-basic"
                                        className="ms-1"
                                    >
                                        Profile
                                    </Dropdown.Toggle>

                                    <Dropdown.Menu>
                                        {/* <Dropdown.Item href="#/action-1">
                                            Create new
                                        </Dropdown.Item>*/}
                                        <Dropdown.Item href="/my-resumes">
                                            My Tempates
                                        </Dropdown.Item>
                                        <Dropdown.Item
                                            onClick={() => {
                                                logout();
                                            }}
                                        >
                                            Logout
                                        </Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </div>
                        )}
                    </div>
                </div>
            </header>
        </div>
    );
}
